}
.home-section {
  margin: 60px 0;
}
.text-left {
  text-align: left;
}
.primary-button {
  color: #ffffff;
  background: #304ffe;
  border: none;
  padding: 5px 17px;
  border-radius: 3px;
  vertical-align: middle;
  font-size: 11pt;
}
.primary-button:hover {
  background: #4f68fc;
  cursor: pointer;
}
.primary-button:focus:not(:focus-visible){
  outline: none;
}
.primary-link {
  color: #2d4bf2;
  text-decoration: none;
  padding: 5px 8px;
  margin: 5px;
  border-radius: 5px;
}
.primary-link:hover {
  background: rgba(0, 0, 0, 0.05);
}
.primary-textfield {
    /*
    min-height: 33px;
    min-width: 220px;
    font-size: 11pt;
    */
    min-height: 37px;
    min-width: 244px;
    font-size: 16px;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);

    -webkit-appearance: none;
    vertical-align: middle;
    border: solid 1px rgb(200, 200, 200);
    border-radius: 3px;
    margin: 5px 0;
    -webkit-box-shadow: none;
            box-shadow: none;
}
.primary-textfield:focus {
  border: solid 1px #304ffe;
  border-radius: 3px;;
  outline: none;
}
.card-button {
    background: #FFFFFF;
    border: solid 1px #E2E2E2;
    color: rgb(20, 20, 20);
    padding: 5px 15px;
    border-radius: 3px;
    text-align: left;
    vertical-align: middle;
    font-size: 11pt;
    padding: 10px 5px;
    min-width: 200px;
}
.card-button:focus:not(:focus-visible) {
  outline: none;
}
.card-button:hover {
    background: #E0E0E0;
    cursor: pointer;
}
.primary-button:disabled {
    background: rgba(48, 79, 254, 0.38);
    cursor: default;
}
input[type=radio] {
    display: none;
}
.primary-radio {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
    display: inline-block;
    padding: 5px 30px;
    position: relative;
    width: auto;
}
.primary-radio::before {
    border: 1px solid #304FFE;
    border-radius: 50%;
    content: '';
    display: block;
    height: 16px;
    left: 5px;
    margin-top: -8px;
    position: absolute;
    top: 50%;
    width: 16px;
}
.primary-radio::after {
    background: #304FFE;
    border-radius: 50%;
    content: '';
    display: block;
    height: 10px;
    left: 8px;
    margin-top: -5px;
    opacity: 0;
    position: absolute;
    top: 50%;
    width: 10px;
}
input[type=radio]:checked+.primary-radio::after {
    opacity: 1;
}
input[type=checkbox] {
    display: none;
}
.primary-checkbox {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
    display: inline-block;
    padding: 5px 30px;
    position: relative;
    width: auto;
}
.primary-checkbox::before {
    border: 1px solid #304FFE;
    border-radius: 1px;
    content: '';
    display: block;
    height: 16px;
    left: 5px;
    margin-top: -8px;
    position: absolute;
    top: 50%;
    width: 16px;
}
.primary-checkbox::after {
    border-right: 2px solid #304FFE;
    border-bottom: 2px solid #304FFE;
    content: '';
    display: block;
    height: 12px;
    width: 6px;
    left: 10px;
    margin-top: -8px;
    opacity: 0;
    position: absolute;
    top: 50%;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.personalrank-label {
    min-width: 60px;
    text-align: right;
    color: #2D4BF2;
}
input[type=checkbox]:checked+.primary-checkbox::after {
    opacity: 1;
}
